// Initialisations
$(function() {

  breakpoints.init();
  devices.init();
  fixers.init();

    // Navigation

    $( ".menu-trigger" ).on('click', function() {
      $(".navigation").slideToggle(500);

    });

    // Escape key handler
    $(document).keyup(function(e) {
        if (e.keyCode == 27) {
            $(".navigation").slideUp(500);
        }
    });

    $( "body" ).on('click', function(event) {

      if(!$(event.target).hasClass("menu-trigger") && !$(event.target).parents("a").hasClass("menu-trigger")){

        $(".navigation").slideUp(500);
      }

    });

    /*
    1. Click on nav item
    2. Remove .active class from all link within that container
    3. Add .active class to currently clicked element
    */

    // Recommendations-section main menu

    $('recommendations-main-menu a').on('click', function(event){

      event.preventDefault();
      $('recommendations-main-menu a').removeClass('active');
      $(this).addClass('active');

      var href = $(this).attr('href'); // Eg: #causes

      $('recommendations-section').hide()
      $('recommendations-section' + href).show();
    })

    // Recommendation-nav 1,2,3

    $('.recommendation-nav a').on('click', function(event){
      event.preventDefault();
      $('.recommendation-nav a').removeClass('active');
      $(this).addClass('active');

      var article = $(this).attr('href');

    $('recommendations-section article').hide();
    $('recommendations-section article' + article).show();

    });


    // Products Information

    $('article a').on('click', function(event){
    event.preventDefault();

    $('article a').removeClass('active');
    $(this).addClass('active');

    var info = $(this).attr('href');

    $('.info').hide();
    $('.info' + info).show();

    });

    // success-stories-menu

    $('success-stories-menu a').on('click', function(event){

      event.preventDefault();
      $('success-stories-menu a').removeClass('active');
      $(this).addClass('active');

      var href = $(this).attr('href'); // Eg: #causes

      $('success-stories-section').hide()
      $('success-stories-section' + href).show();
    })

    // Skin Care Problems Page

    // Animation
    $('skin-care-problems .crossicon').animate({
      'width': '35px', 'height': '35px'
    }, 1000 ).animate({
      'width': '30px', 'height': '30px'
    }, 1000 );



  // Show problem on click
  $('skin-care-problems a').on('click', function(event){

    event.preventDefault();
    var href = $(this).attr('href');

    if($('skin-care-problems div').hasClass('visible')){

       $('skin-care-problems div').removeClass('visible');
    } else {
        $('skin-care-problems div').removeClass('visible');
        $('skin-care-problems div' + href).addClass('visible');
    }
  });


$('skin-care-problems a').on('click', function(event){

  event.preventDefault();

  if( ($(event.target).hasClass('left') || $(event.target).parents('a').hasClass('left')) && $('skin-care-problems div').hasClass('visible') ){

    $('symptoms-box-right').show();
  }else{
    $('symptoms-box-right').hide();
  }

  if( ($(event.target).hasClass('right') || $(event.target).parents('a').hasClass('right')) && $('skin-care-problems div').hasClass('visible') ){

    $('symptoms-box-left').show();
  }else{
    $('symptoms-box-left').hide();
  }

});
//skin-care-treatments-menu (Mobile)


      $('.mobile-nav-treatments').on('click', function(event){

        event.preventDefault();

        var href = $(this).attr('href');

        if($(this).hasClass('active') ){
          $( 'skin-care-treatments' + href).slideUp();
          $('.mobile-nav-treatments').removeClass('active');
        } else {
          $('skin-care-treatments').not(href).slideUp();
          $( 'skin-care-treatments' + href).slideDown();
          $('.mobile-nav-treatments').removeClass('active');
          $(this).addClass('active');
        }

      });

//success-stories-section-mob-menu (Mobile)


        $('.success-stories-mobile-nav').on('click', function(event){

          event.preventDefault();

          var href = $(this).attr('href');

          if($(this).hasClass('active') ){
            $( 'success-stories-section-mob' + href).slideUp();
            $('.success-stories-mobile-nav').removeClass('active');
          } else {
            $('success-stories-section-mob').not(href).slideUp();
            $( 'success-stories-section-mob' + href).slideDown();
            $('.success-stories-mobile-nav').removeClass('active');
            $(this).addClass('active');
          }
      });


//mobile-main-nav-sub-page-main menu (Mobile)

      $('.mobile-main-nav-sub-page').on('click', function(event){

        event.preventDefault();

        var href = $(this).attr('href');

        if($(this).hasClass('active') ){
          $( 'mobile-sub-page-content' + href).slideUp();
          $('.mobile-main-nav-sub-page').removeClass('active');
        } else {
          $('mobile-sub-page-content').not(href).slideUp();
          $( 'mobile-sub-page-content' + href).slideDown();
          $('.mobile-main-nav-sub-page').removeClass('active');
          $(this).addClass('active');
        }

      });

      //mobile-sub-page-nav menu (Mobile)

            $('.mobile-sub-page-nav').on('click', function(event){

              event.preventDefault();

              var href = $(this).attr('href');

              if($(this).hasClass('active') ){
                $( 'products-mobile' + href).slideUp();
                $('.mobile-sub-page-nav').removeClass('active');
              } else {
                $('products-mobile').not(href).slideUp();
                $( 'products-mobile' + href).slideDown();
                $('.mobile-sub-page-nav').removeClass('active');
                $(this).addClass('active');
              }

              console.log(href);

            });


    //Slick Slider

    $('.slider').slick({
      slidesToShow: 4,
      slidesToScroll: 4
    });

    var filtered = false;

    $('.js-filter').on('click', function(){
      if (filtered === false) {
        $('.filtering').slick('slickFilter',':even');
        $(this).text('Unfilter Slides');
        filtered = true;
      } else {
        $('.filtering').slick('slickUnfilter');
        $(this).text('Filter Slides');
        filtered = false;
      }
    });

});
