var html = $("html"),
    body = $("body");

// Browser Detection
var browser = {
  name : html.data('browser') ? html.data('browser').split(' ')[0] : null,
  version : html.data('browser') ? html.data('browser').split(' ')[1] : null
};

// Device Detection
var mobile, tablet, desktop, devices = {
  init : function() {
    mobile = devices.mobile(),
    tablet = devices.tablet(),
    desktop = devices.desktop();
  },
  mobile : function(tablet) {
    if (typeof tablet !== undefined && tablet === true) {
      return typeof html.data('device') !== 'undefined' && html.data('device') == 'mobile' && tablet();
    } else {
      return typeof html.data('device') !== 'undefined' && html.data('device') == 'mobile';
    }
  },
  tablet : function() {
    return typeof html.data('device') !== 'undefined' && html.data('device') == 'tablet';
  },
  desktop : function() {
    return typeof html.data('device') !== 'undefined' && html.data('device') == 'desktop';
  }
}

// Custom Properties (CSS Variables)
var customProperties = getComputedStyle(document.body);

// Breakpoints
// Query the computed CSS file for Custom Properties (css variables) specifcially for breakpoints
var breaks = [],
breakpoints = {
  names : ['max', 'large', 'medium', 'small', 'min'],
  init : function() {
    for (var i = 0; i < breakpoints.names.length; i++) {
      breaks[breakpoints.names[i]] = +customProperties.getPropertyValue('--breakpoint-'+breakpoints.names[i]);
    }
  }
}
